<template>
	<view>
		<view 
			class="swiper-box"
			:style="{height: rdata.params.height + 'px'}"
			style="overflow: hidden;"
		>
			<swiper
				:circular="true"
				:style="{height:'100%',borderRadius: rdata.params.border+'px',paddingTop: rdata.params.marginTop+'px',paddingBottom: rdata.params.marginBottom + 'px',paddingLeft: rdata.params.marginLeft + 'px',paddingRight: rdata.params.marginRight + 'px'}"
				:indicator-dots="swiper.indicatorDots" 
				:autoplay="true" 
				:interval="rdata.params.duration"
				:duration="swiper.duration"
				indicator-active-color="#b71c1c"
			 >
				<swiper-item 
					class="have-none" 
					v-for="(item, index) in rdata.params.list" 
					:key="index"
					@click="jump(item)"
				>
					<image 
					 :src="item.thumb_image" 
					 mode="scaleToFill" 
					 :style="{height: rdata.params.height+'px',borderRadius: rdata.params.border+'px'}"
					 ></image>
				</swiper-item>
			</swiper>
		</view>
	</view>
</template>

<script>
	export default {
		props:{
		    rdata:{
		    	required: true
		    },
		},
		data() {
			return {
				swiper: {
					indicatorDots: true,
					autoplay: true,
					duration: 500,
				}
			};
		},
		mounted() {},
		methods: {
			jump(value){
				this.$common.jump(value);
			}
		}
	}
</script>

<style lang="scss" scoped>
	.swiper-box{ 
		width: 100%; margin: 0 auto; 
		image {
			display: block;width: 100%;height: 150px;background-color: #fff;pointer-events: none;
		}
	}
</style>